import Vue from 'vue'
import VueRouter from 'vue-router'
// import { component } from 'vue/types/umd'
import Home from '../views/Home.vue'
import Main from '../views/Main.vue'
import Classify from '../views/Classify.vue'
import Article from '../views/Article.vue'
import Asclassify from '../views/Asclassify.vue'
// import { component } from 'vue/types/umd'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Main,
    children: [
      {
        path: '/',
        name: 'home',
        component: Home
      },
      {
        path: '/classifies',
        name: 'classify',
        component: Classify,
        children: [
          {
            path: '/pages/:id',
            name: 'page',
            component: Asclassify,
            props: true
          }

        ]
      },
      {
        path: '/articles/:id',
        name: 'article',
        component: Article,
        props: true
      },

    ]
  }
]
const router = new VueRouter({
  routes
})

export default router
