<template>
  <div id="app">
    <div v-if="model">
      <div v-for="(item) in models" :key="item.id">

        <router-link tag="div" :to="`/articles/${item._id}`">
          <div class="item">
            <!-- <p style="color:rgb(33, 200, 00)">{{items.title}}</p> -->
            <div class="content">
              <div>
                <b-card no-body class="overflow-hidden" style="max-width: 100%">
                  <b-row no-gutters>
                    <b-col md="6">
                      <b-card-img :src="item.icon" class="rounded-0"></b-card-img>
                    </b-col>
                    <b-col md="6">
                      <b-card-body :title="item.title">
                        <div class="text">
                          <b-card-text v-html="item.content">
                          </b-card-text>
                        </div>
                      </b-card-body>
                    </b-col>
                  </b-row>
                </b-card>
              </div>

            </div>
          </div>
        </router-link>

        <!-- <h1>dasdada asdcaxc</h1> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: { required: 'true' }
  },
  data() {
    return {
      models: null,
      model: null,
      title: null,
      content: null
    }
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`pages/${this.id}`)
      this.model = res.data
      // console.log(this.model);
      this.models = this.model[0].newsList
      // this.content = this.model[0].newsList.content
      // // console.log(this.title);
      //   console.log(this.model[0].newsList[0].title);
    }
  },
  created() {
    this.fetch()
  },
  // updated() {
  //   this.fetch()
  // },


}
</script>

<style lang="scss" scoped>
.item {
  margin-bottom: 2.5rem;
}
p {
  display: block;
  width: 100%;
  border-left: 0.125rem solid rgb(197, 133, 199);
  padding-left: 0.625rem;
  margin: 0.625rem 0;
}
.text {
  display: -webkit-box; /*作为弹性伸缩盒子模型显示*/
  -webkit-line-clamp: 3; /*显示的行数；如果要设置2行加...则设置为2*/
  overflow: hidden; /*超出的文本隐藏*/
  text-overflow: ellipsis; /* 溢出用省略号*/
  -webkit-box-orient: vertical; /*伸缩盒子的子元素排列：从上到下*/
}
</style>