<template>
  <div>
    <div class="header">
      <div v-for="(item) in newCats.slice(1)" :key="item.index" @click="up">
        <router-link tag="div" :to="`/pages/${item._id}`">
          <b-badge pill :style="randomRgb()" style="float:left ;margin:10px">{{item.name}}</b-badge>
        </router-link>
      </div>
    </div>
    <div id="footer">
      <router-view>
      </router-view>
    </div>

  </div>
</template>

<script>
export default {

  data() {
    return {
      newCats: [
        // success, danger, warning, info, light, dark
      ]

    }
  },
  methods: {
    async fetchNewsCats() {
      const res = await this.$http.get('news/list')
      this.newCats = res.data
    },
    //随机颜色
    randomRgb: function () {
      var R = Math.floor(Math.random() * 255);
      var G = Math.floor(Math.random() * 255);
      var B = Math.floor(Math.random() * 255);
      return { background: 'rgb(' + R + ',' + G + ',' + B + ')' };
    },
    up() {

      // alert('提示刷新将丢失数据')
      // // 如果可以取消就用　confirm
      window.location.reload()

    }

  },
  created() {
    this.fetchNewsCats()
  },
}



</script>

<style >
.header {
  cursor: pointer;
}
#footer {
  width: 100%;
  padding-top: 30px;
  overflow: hidden;
  border-top: 1px solid rgb(187, 180, 181);
}
</style>