<template>
  <div>
    <p style="color:pink ; margin-bottom:25px;" class="title"><b>{{title}}</b></p>
    <div v-html="content">{{content}}</div>
  </div>

</template>

<script>
export default {
  props: {
    id: { required: 'true' }
  },
  data() {
    return {
      model: null,
      title: null,
      content: null
    }
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`articles/${this.id}`)
      this.model = res.data
      this.title = this.model.title
      this.content = this.model.content
      console.log(this.model);
    }
  },
  created() {
    this.fetch()
  }


}
</script>

<style>
.title {
  border-left: 3px solid rgb(161, 247, 130);
  padding-left: 1.25rem;
  height: 1.875rem;
  line-height: 1.875rem;
  font-size: 24px;
}
</style>